
      @use 'sass:math';
      @use 'sass:color';
      @import '/Users/nickmajor/projects/rw/care-ops-frontend/src/scss/provider-variables.scss';
    
.opt-in__icon {
  color: $black-20;
  margin-bottom: 24px;
  text-align: left;

  .icon {
    font-size: 48px;
    width: 1em;
  }

  &.opt-in__icon--success {
    color: $success-color;
  }

  &.opt-in__icon--warn {
    color: $warning-color;
  }

  &.opt-in__icon--error {
    color: $error-color;
  }
}

.opt-in__heading-text {
  font-size: 20px;
  margin-bottom: 32px;
  text-align: left;
}

.opt-in__field {
  margin-bottom: 16px;
  text-align: left;
}

.opt-in__field-label {
  display: block;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 8px;
}

.opt-in__field-input {
  color: $black-20;
  display: block;
  padding: 12px 16px;
  width: 100%;

  &.has-error {
    border-color: $error-color;
    color: $error-color;
    margin-bottom: 6px;
  }
}

.opt-in__disclaimer {
  font-size: 14px;
  margin-top: 32px;
  text-align: left;
}

.opt-in__submit {
  height: 40px;
  margin-top: 16px;
}
